@import "@/assets/variables";

.options-wrapper {
  &+.options-wrapper {
    margin-top: 15px;
  }
}

.options-name {
  font-weight: 700;
  margin-bottom: 10px;
}

.options {
  display: flex;
  gap: 10px;

  @include tablet {
    gap: 5px;
  }

  &__option {
    flex-grow: 1;
    border-radius: 8px;
    padding: 15px;
    background-image: $my-buy-options-gradient;
    color: #fff;
    font-weight: 500;
    text-align: center;
    cursor: pointer;

    @include tablet {
      padding: 10px;
    }
  }

  &__time {
    font-size: 16px;
    margin-bottom: 5px;

    @include tablet {
      font-size: 14px;
    }
  }

  &__price {
    font-size: 20px;
    white-space: nowrap;

    @include tablet {
      font-size: 14px;
      font-weight: 700;
    }
  }
}

.tip {
  font-size: 12px;
  margin-top: 5px;
}
